*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container{
  max-width: 1350px;
  padding: 0 10px;
  margin: 0 auto;
}

.header{
  padding: 20px 0;
  border-bottom: 1px solid rgb(184, 184, 184);
  font-size: 18px;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  background-color: white;
}

.header__container{
  text-align: right;
}
.header__menu{
  list-style: none;
  display: flex;
  gap: 20px;
  justify-content: end;
}
.header__menu li{
  cursor: pointer;

}
.header__menu li.active{
  text-decoration: underline;
}


.about{
  margin: 100px 0 0 0;
  text-align: center;
}

.about__img{
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: 4px solid; 
  background-image: linear-gradient(to right, rgb(21, 255, 255), #2be256);  
  padding: 2px;
  object-fit: cover;
  margin-bottom: 20px;
}

.about__title{
  font-size: 30px;
}

.about__info {
  text-align: left;
  line-height: 150%;
  font-size: 17px;
  margin: 25px 0 0 0;
}

.contacts{
  margin: 50px 0 50px 0;
}
.contacts__container{
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  justify-content: space-between;
}

.contacts_item_body {
  text-align: center;
  width: 290px;
  height: 310px;
  border-radius: 15px;
  box-shadow: #e8e8e8 0 0 50px;
}
.contacts_item_logo {
  padding: 20px 0 10px 0;
}
.contacts_item_logo img {
  width: 65px;
  height: 65px;
}
.contacts .title h1 {
  margin-bottom: 30px;
}
.contacts_item_description {
  font-family: 'Mukta', sans-serif;
  font-size: 18px;
  color: #414141;
}
.contacts_tg-btn {
  background-color: #21AAE5;
  transition: box-shadow .4s ease;
}
.contacts_tg-btn {
  background-color: #21AAE5;
  transition: box-shadow .4s ease;
}
.contacts_mail-btn {
  background-color: #D54B3D;
  transition: box-shadow .4s ease;
}
.contacts_instagram-btn {
  background: linear-gradient(to right, #f9ed32 10%, #ee2a7b 90%);
  transition: box-shadow .4s ease;
}
.contacts_vk-btn {
  background-color: #1976D2;
  transition: box-shadow .4s ease;
}
.contacts_pm_btn {
  font-family: 'Balsamiq Sans', sans-serif;
  margin: 45px 0 0 0;
  width: 210px;
  padding: 10px 0;
  border-radius: 10px;
  font-size: 14px;
  font-weight: 600;
  text-decoration: none;
  color: #fff;
  display: inline-block;
}




.works{
  margin: 100px 0 0 0;
}

.works__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 20px;
}


.item-works {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  margin: 15px;
  transition: transform 0.3s, box-shadow 0.3s;
  width: 300px; 

}


.item-works:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
}


.item-works__img {
  width: 100%;
  height: auto;
  display: block;
}


.item-works__description {
  padding: 15px;
  text-align: center;
}


.item-works__description p {
  font-size: 16px;
  color: #333;
  margin: 0 0 10px;
  line-height: 1.5;
}


.item-works__link {
  display: inline-block;
  padding: 10px 15px;
  background-color: #007bff; 
  color: #fff; 
  border-radius: 5px;
  text-decoration: none;
  transition: background-color 0.3s;
}


.item-works__link:hover {
  background-color: #0056b3; 
}


@media (max-width: 600px) {
  .item-works {
      width: 90%; 
  }
}